/* Base styles */

@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

/* Modal overlay */
.order-summary-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Modal container */
.order-summary-modal-container {
  background: white;
  overflow: auto;
  max-height: 600px;
  border-radius: 10px;
  padding: 20px;
  max-width: 751px;
  width: 100%;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}
.order-summary-modal-container::-webkit-scrollbar {
  width: 0;
}
.order-summary-modal-container::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}
.order-summary-modal-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}
.order-items-ordersummary {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
/* Success message */
.success-message {
  position: relative;
  top: 0;
  left: 50%;
  background: #1bd27a;
  width: 330px;
  color: #ffffff;
  text-align: center;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  align-self: center;
}

/* Order details */
.order-details {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Order header */
.order-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.order-id-summary {
  color: #dc3545;
  font-size: 18px;
  font-weight: 800;
  font-family: "Poppins", sans-serif;
  font-style: normal;
}

.order-date {
  color: #666;
  font-size: 15px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: flex-end;
}
.invoice-btn {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #eb5757;
  width: 100px;
  height: 36px;
  border: white;
  font-size: 14px;
  font-weight: 500;
  border-radius: 10px;
  font-family: "Poppins", sans-serif;
}
.path-order {
  color: #959595;
  font-size: 10px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.path-order span {
  color: #dddddd;
  font-weight: 400;
  font-size: 20px;
}
.estimate-time {
  color: #28a745;
  font-family: "Poppins";
  font-size: 13px;
}
.seprationline-ordersummary {
  border-top: 1px solid #dddddd;
}
/* Order items */
.order-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 5px;
  border-bottom: 1px solid #ddd;
  background-color: #f6f6f6;
  border-radius: 12px;
}

.order-item img {
  width: 50px;
  height: 50px;
  border-radius: 5px;
  object-fit: cover;
}

.order-item-details {
  flex-grow: 1;
  padding: 0 10px;
}
.chicken-shawrma {
  font-size: 17px;
  font-weight: 600;
  font-family: "Poppins", sans-serif;
}
.three {
  font-size: 16px;
  color: gray;
  margin-right: 8px;
}
.qty {
  font-size: 16px;
  color: gray;
}

.extra {
  font-size: 13px;
  color: grey;
  font-family: "Poppins", sans-serif;
}
/* Payment and Address */
.payment-address {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.order-summary {
  margin-top: 11px;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  /* font-weight: bold; */
  color: grey;
  border-top: 1px solid #ddd;
}
.order-summary div {
  margin: 5px 0;
  /* border-top: 1px solid #ddd; */
}
.hotel-address {
  color: gray;
  font-family: "Poppins";
  font-size: 14px;
  /* border-top: 1px solid #ddd; */
}

.price {
  float: right;
}

.discount,
.tax {
  color: #ccc;
}

.total-price {
  font-size: 1.2em;
  color: black;
}

/* Summary and Help */
.summary-help {
  display: flex;
  /* justify-content: space-between; */
  /* margin-top: 20px; */
}

.summary-help a {
  color: #007bff;
  text-decoration: none;
}
.countDown-summary {
  text-align: center;
  color: #f44336;
}
