.Resturant-page {
  padding: 0 3.5rem;
}
@media (max-width: 768px) {
  .Resturant-page {
    padding: 0 1rem;
  }
}
.choose-takeaway-dinein {
  padding: 0 3.5rem;
  font-weight: 300;
  font-size: 18px;
  color: #797979;
  cursor: pointer;
  letter-spacing: 4px;
}
.choose-takeaway-dinein .active {
  font-weight: 600;
  font-size: 20px;
  color: #eb5757;
  border-bottom: 2px solid #eb5757;
}
.time-slot-pickup {
  opacity: 0;
}
.time-slot-pickup.show {
  opacity: 1;
}
.people-count {
  opacity: 0;
  max-height: 0;
  overflow: hidden;
}

.people-count.show {
  opacity: 1;
  max-height: 100%;
  transition: opacity 1s ease, max-height 1s ease;
}
.Filter-button {
  background-color: #eb5757;
  border-radius: 3rem;
  color: white;
  border: none;
  width: 6rem;
  height: 2rem;
}
.Filter-button-search-last {
  background-color: #eb5757;
  border-radius: 3rem;
  color: white;
  border: none;
  width: 6rem;
  height: 2rem;
}
.Search-bar-input {
  border: none;
  box-shadow: 0px 4px 4px rgba(199, 9, 9, 0);
  outline: none;
}
.Rpage-header {
  display: flex;
  justify-content: space-between;
}
.Rpage-header-Text {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
/* Adjust header text for smaller screens */
@media (max-width: 768px) {
  .Rpage-header-Text {
    justify-content: center;
    width: 100%;
  }
}
.Filter-button-search {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
}
.food-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.items-catagories {
  width: 10%;
  text-align: right;
}
@media (max-width: 768px) {
  .items-catagories {
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: space-evenly;
    overflow: auto;
    gap: 0.75rem;
  }
}
.items-catagories h5 {
  margin: 3rem 0;
}
.item-list {
  width: 50%;
}
@media (max-width: 768px) {
  .item-list {
    width: 100%;
  }
}
.cart-section {
  width: 30%;
  display: flex;
  flex-direction: column;
}
@media (max-width: 768px) {
  .cart-section {
    width: 100%;
  }
}
.item-food {
  display: flex;
  margin-bottom: 1rem;
  justify-content: space-between;
  padding-top: 2rem;
}
.Add-food {
  width: 4rem;
  background-color: #eb5757;
  border-radius: 5px;
  color: white;
  border: none;
}
.cart-food {
  padding: 0;
  align-items: center;
}
.cart-add-food {
  width: 2rem;
  height: 3rem;
}
.button-cart {
  align-items: center;
  display: flex;
  border-radius: 40px;
}
.hline {
  align-self: center;
  border-bottom: 1px solid black;
  margin-top: 1rem;
  width: 100%;
}
.cart-text {
  display: flex;
  justify-content: space-between;
}
.next-button {
  width: 100%;
  height: 2rem;
}
.next-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}
.button-time-slot {
  background-color: #fff3f2;
  border: none;
  width: 6.8rem;
  height: 2.8rem;
  border-radius: 10px;
  color: #797979;
  font-weight: 200;
  font-size: 16px;
}
.timeslot-button {
  justify-content: flex-start;
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  margin-bottom: 10px;
}
.timeslot-selected {
  background-color: #eb5757;
  color: white;
}
.timeslot-button button:hover {
  cursor: pointer;

  border: 2px solid rgba(255, 0, 0, 0.616);
}
.text-food-item p {
  margin-left: 40px;
  color: #797979;
}
.text-food-item-cart {
  display: flex;
  gap: 1rem;
}
.cartitem-text p {
  color: #797979;
}
