/* src/components/MainContent.css */
.main-content-restaurant {
  padding: 40px;
  text-align: center;
}

.hero-restaurant {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.hero-image-restaurant {
  border-radius: 50%;
  width: 400px;
  height: 400px;
}

.hero-text-restaurant h1 {
  font-size: 2.5rem;
  color: #333;
  text-align: left;
  /* margin-left: 9rem; */
}

.hero-text-restaurant .highlight-restaurant {
  color: #ff5a5f;
}

.hero-text-restaurant p {
  margin-top: 10px;
  color: #111010;
  font-weight: 640;
  text-align: left;
}

.cta-button-restaurant {
  background-color: #ff5a5f;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 20px;
  margin-top: 20px;
  /* margin-left: -13rem; */
  margin-right: 13rem;
}

.search-filters-container-restaurant {
  display: flex;
  justify-content: center;
  margin-top: 40px;
}

.search-filters-restaurant {
  width: 100%;
  margin-top: 6rem;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0 2px 4px rgba(0, 0, 0.1, 0.1);
}

.search-filters-restaurant select,
.search-filters-restaurant input {
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 11px;
  height: 32px;
  width: 112px;
}

.search-filters-restaurant .search-button-restaurant {
  background-color: #333;
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 5px;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .hero-restaurant {
    flex-direction: column;
  }

  .hero-image-restaurant {
    width: 300px;
    height: 300px;
  }

  .search-filters-restaurant {
    flex-direction: column;
    gap: 20px;
  }
}

.restaurantpage-resturant {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 20px;
  margin-top: 20px;
}

.restaurantpage-resturant .product-caraosel {
  /* flex: 1 1 calc(25% - 20px); Adjusted for 4 items per row */
  width: 280px;
  height: 400px;
  background: whitesmoke;
  margin: 0 0 20px 0;
  position: relative;
  transition: 0.5s;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 7px;
}

.restaurantpage-resturant .product-caraosel picture {
  width: 100%;
  height: 245px; /* Fixed height for consistency */
  overflow: hidden;
}

.restaurantpage-resturant .product-caraosel picture img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures image covers the container without distortion */
  border-radius: 10px;
}
.restaurantpage-resturant .product-caraosel .detail p {
  text-align: left;
}

.location-caraosel {
  width: 90%;
  text-align: left;
  left: 5px;
  position: relative;
  font-size: 23px;
  top: -40px;
  margin-left: 10px;
}
.restaurantpage-resturant .time-caraosel {
  width: 90%;
  text-align: left;
}
.resturant-page-detail {
  width: 100%;
  margin-top: 45px;
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  justify-content: space-between;
  font-size: 20px;
  position: relative;
  top: -40px;
  padding: 5px;
  color: #0c0c0c;
  margin-left: 10px;
  text-align: left;
}
.resturant-page-detail p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@media (max-width: 768px) {
  .hero-restaurant {
    flex-direction: column;
  }

  .hero-image-restaurant {
    width: 300px;
    height: 300px;
  }

  .search-filters-restaurant {
    flex-direction: column;
    gap: 20px;
  }

  .cta-button-restaurant {
    margin-right: 0;
    margin-top: 10px;
  }

  /* .restaurantpage-resturant .product-caraosel {
    flex: 1 1 100%; 
    margin: 0 0 20px 0;
  } */
}
