.Resturant-header {
  height: max-content;
  background-color: #2f3034;
}
.image-grid {
  width: max-content;
  display: grid;
  grid-template-columns: repeat(2, 100px);
  grid-template-rows: repeat(2, 100px);
  align-content: center;
}
.img-grid {
  width: 100px;
  height: 100px;
  object-fit: cover;
}
.content-header {
  display: flex;
  gap: 2rem;
  padding: 20px;
}
.content-text-header {
  display: flex;
  flex-direction: column;
  text-align: start;
  color: white;
  justify-content: space-evenly;
}
.ratting-header {
  display: flex;
  gap: 0.5rem;
}
.vline {
  border-left: 1px solid white;
  margin: 0 10px;
}
@media (min-width: 600px) {
  .content-header {
    flex-direction: row;
    align-items: flex-start;
  }

  .content-text-header {
    text-align: start;
    align-items: flex-start;
  }

  .ratting-header {
    justify-content: flex-start;
  }
}

@media (min-width: 900px) {
  .content-header {
    gap: 4rem;
    padding: 40px;
  }
}
