.cart {
  min-height: 200px;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  margin: 20px auto;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  background: white;
  width: 500px;
  position: relative;
}
.cart-dropdown {
  position: absolute;
  top: 50px;
  right: 8rem;
  z-index: 1000;
}
.cart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.close-icon {
  cursor: pointer;
  text-align: center;
  font-size: 20px;
  width: 30px;
  height: 30px;
  background-color: #00000059;
  border-radius: 100px;
  border: none;
}

.cart h2 {
  text-align: center;
  color: #797979;
  font-size: 16px;
  font-weight: 700;
}

.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 6px;
  margin-bottom: 8px;
  background-color: #f6f6f6;
  border-radius: 12px;
  border-bottom: 1px solid #ddd;
  transition: transform 0.3s ease-in-out;
}

.cart-item-info {
  display: flex;
  align-items: center;
}

.cart-item-info img {
  margin-right: 10px;
}

.delete-icon {
  cursor: pointer;
  color: red;
}

.coupon-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* margin-bottom: 20px; */
}

.coupon-section input {
  flex-grow: 1;
  padding: 10px;
  margin-right: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.coupon-section button {
  padding: 10px 20px;
  border: none;
  background-color: #ff6f61;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  margin-bottom: 20px;
}

.cart-checkout-button {
  display: block;
  width: 100%;
  padding: 10px;
  border: none;
  background-color: #ff6f61;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}
.content-cart > p:first-child {
  font-weight: 700;
  font-size: 16px;
  color: #1e1e1e;
}

.price-quantity-cart {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  color: #797979;
}
.seperationline-cart {
  border-right: 2px solid #797979;
  height: 16px;
}
@media (max-width: 768px) {
  .cart-dropdown {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 50%;
    translate: -50% 0%;
    z-index: 10;
    display: grid;
    place-items: center;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1000;
    overflow: auto;
  }
}
