.floating-message {
  position: fixed;
  bottom: 0;
  margin: 20px;
  z-index: 90;
  display: flex;
  gap: 1rem;
  left: 50%;
  translate: -50% 0%;
  padding: 10px;
  background-color: #f8fbfc;
  border-radius: 120px;
  border: 1px solid #d0d5dd;
  align-items: center;
  width: max-content;
}
.floating-message-time {
  background-color: #aef5d3;
  color: #067647;
  border-radius: 10px;
  font-size: 14px;
  padding: 5px;
  font-weight: 600;
}
.floating-message-content {
  font-size: 14px;
  font-weight: 600;
  color: #344054;
  display: flex;
  gap: 0.5rem;
}
.arrao-floating-message {
  display: flex;
  align-items: center;
}
