.select-number-container {
  background-color: #fff;
  /* border-radius: 15px; */
  /* box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.1); */
  margin: 0 auto;
  text-align: center;
  margin-top: 2rem;
}

.select-number-container h2 {
  color: #333;
  font-size: 1.5em;
  margin-bottom: 20px;
  display: flex;
}

.options {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.option {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 150px;
  height: 57px;
  padding: 10px 25px;
  margin: 10px;
  border: 1px solid #ccc;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s;
  background-color: #fff3f2;
}

.option span {
  color: #333;
  font-size: 12px;
  font-weight: 400;
}
.option:hover {
  border: 2px solid #eb5757;
}
.option.selected {
  background-color: #eb5757;
  border-color: #eb5757;
}
.option.selected span {
  color: white;
}
.person-image.selected {
  filter: brightness(0) saturate(100%) invert(100%);
}
.option box-icon {
  fill: #ff4b4b;
}

.more-than-five {
  margin-bottom: 20px;
}

.more-than-five p {
  font-size: 1.2em;
  color: #666;
  display: flex;
  font-weight: 600;
}

.more-than-five input {
  width: 100%;
  height: 2rem;
  padding: 10px 20px;
  border: 1px solid #ccc;
  border-radius: 12px;
  font-size: 1em;
}

.checkout-button {
  width: 100%;
  height: 2rem;
  background-color: #ff4b4b;
  color: #fff;
  border: none;
  border-radius: 12px;
  font-size: 1em;
  cursor: pointer;
}

.checkout-button:hover {
  background-color: #e04040;
}
.checkout-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}
