.profile-dropdown {
  position: absolute;
  top: 50px;
  right: 5rem;
  z-index: 1000;
}

.sidebar {
  width: 428px;
  height: auto;
  padding: 20px;
  background-color: #fff;
  border-right: 1px solid #ddd;
  box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
}
.sidebar-close {
  min-height: 400px;
  margin-top: 20px;
}

.profile-header {
  display: flex;
  text-align: center;
  margin-bottom: 20px;
  justify-content: space-between;
}
.profile-avatar-completion {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.profile-avatar {
  flex: 0 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  align-self: center;
  /* background-color: #d1d1d1; */
  /* border-radius: 50%; */
  /* margin: 0 auto 10px; */
}
.profile-info {
  flex: 1;
  text-align: left;
  margin-left: 10px;
  align-self: center;
}

.profile-info h2 {
  font-size: 16px;
  text-align: left;
  /* margin: 10px 0 5px; */
}

.profile-info p {
  color: #888;
  margin: 0;
  font-size: 14px;
}

.profile-completion {
  flex: 0 0 auto;
  position: relative;
  width: 80px;
  height: 80px;
  /* margin: 10px auto; */
}

.completion-circle {
  position: relative;
  width: 100%;
  height: 100%;
}
.progress-ring {
  transform: rotate(-90deg);
}

.progress-ring__circle {
  transition: 0.35s stroke-dashoffset;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}
.circle {
  position: absolute;
  width: 100%;
  height: 100%;
  clip: rect(0px, 80px, 80px, 40px);
}

.mask {
  width: 100%;
  height: 100%;
  clip: rect(0px, 40px, 80px, 0px);
}

.full,
.half {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #eaeaea;
  border-radius: 50%;
}

.fill {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #f00;
  border-radius: 50%;
  clip: rect(0px, 40px, 80px, 0px);
  transform: rotate(216deg); /* 60% of 360 degrees */
}

.inside-circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1em;
  color: #f00;
}

.profile-warning {
  background-color: #f8d7da;
  color: #100405;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.profile-warning a {
  color: #807c7c;
  font-weight: 400;
  text-decoration: underline;
}

.sidebar-nav ul {
  list-style: none;
  padding: 0;
}

.sidebar-nav li {
  margin: 10px 0;
}

.sidebar-nav a {
  color: #333;
  text-decoration: none;
  font-weight: bold;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.sidebar-nav a:hover {
  text-decoration: underline;
}

.sidebar-nav box-icon {
  margin-right: 10px;
}
.close-span-profile {
  font-size: 20px;
  width: 30px;
  height: 30px;
  background-color: #00000059;
  border-radius: 100px;
  border: none;
  cursor: pointer;
}
.profile-header-content {
  display: flex;
}
.sidebar-nav {
  margin: 0;
  width: 100%;
  display: block;
}
.sidebar-nav ul li:nth-child(5) a {
  color: #eb5757;
}
.sidebar-nav ul li a img {
  margin-right: 10px;
}
.profile-line {
  border-bottom: 1px solid #ffa1a1;
  margin-bottom: 20px;
}
@media (max-width: 768px) {
  .profile-dropdown {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 50%;
    translate: -50% 0%;
    z-index: 10;
    display: grid;
    place-items: center;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1000;
    overflow: auto;
  }
}
