.container,
.container form,
.input-field,
header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.container {
  position: relative;
  width: 468px;
  height: 401px;
  background: #fff;
  padding: 30px 65px;
  border-radius: 30px;
  row-gap: 20px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
}
.container header {
  height: 65px;
  width: 65px;
  /* background: #4070f4; */
  color: #fff;
  font-size: 2.5rem;
  border-radius: 50%;
}
.close {
  color: #aaa;
}
.close-span-otp {
  position: relative;
  z-index: 1;
  left: 420px;
  top: 60px;
  font-size: 40px;
}
.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
.container h4 {
  font-size: 1.25rem;
  color: #333;
  font-weight: 500;
}
form .input-field {
  flex-direction: row;
  column-gap: 10px;
}
.input-field input {
  height: 45px;
  width: 42px;
  border-radius: 6px;
  outline: none;
  font-size: 1.125rem;
  text-align: center;
  border: 1px solid #ddd;
}
.input-field input:focus {
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
}
.input-field input::-webkit-inner-spin-button,
.input-field input::-webkit-outer-spin-button {
  display: none;
}
form button {
  margin-top: 25px;
  width: 100%;
  color: #fff;
  font-size: 1rem;
  border: none;
  padding: 9px 0;
  cursor: pointer;
  border-radius: 6px;
  pointer-events: none;
  background: #eb9a6a;
  transition: all 0.2s ease;
}
form button.active {
  background: #f90606;
  pointer-events: auto;
}
form button:hover {
  background: #f10e0e;
}
.first-name-otp {
  position: absolute;
  width: 277px;
  height: 36px;
  left: 60px;
  top: 36px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 800;
  font-size: 24px;
  line-height: 36px;
  color: #f44336;
}

.enter-otp {
  width: 68px;
  height: 21px;

  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  /* identical to box height */

  /* Text/Primary */
  color: #312e49;
  /* Inside auto layout */
  flex: none;
  order: 0;
  flex-grow: 0;
}
.tag {
  top: 87px;
  left: 60px;
}
.fname {
  white-space: nowrap;
}
input:disabled {
  cursor: default;
  background-color: light-dark(rgba(239, 239, 239, 0.3), rgba(59, 59, 59, 0.3));
  color: light-dark(rgb(84, 84, 84), rgb(170, 170, 170));
}
