/* @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap'); */
.main-caraosel {
  width: 100%;
  height: 704px;
  /*background: red;*/
  margin: 10px auto;
  position: relative;
  padding: 192px 0;
}
.main-caraosel .text {
  padding-left: 25px;
  color: #554;
}
.main-caraosel .text h1 {
  font-size: 50px;
}
.main-caraosel .text p {
  width: 60%;
  padding: 5px;
  margin: auto;
  line-height: 30px;
}
.main-caraosel header {
  width: 98%;
  height: -10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 20px;
  justify-content: space-between;
}

.main-caraosel header p span {
  font-size: 30px;
  margin: 0 5px;
  cursor: pointer;
  color: #161515;
  width: 40px;
  height: 40px;
  display: inline-block;
  line-height: 37px;
  text-align: center;
  border-radius: 20px;
}
.main-caraosel header p span:hover {
  background: #eb5757;
  color: white;
}
.section-caraosel {
  width: 98%;
  height: 600px;
  margin: auto;
  display: flex;
  overflow-x: auto;
}
.section-caraosel::-webkit-scrollbar {
  display: none;
}
.section-caraosel .product-caraosel {
  min-width: 300px;
  height: 75%;
  background: #f5f5f5;
  padding: 7px;
  margin: 0 20px 0 0;
  position: relative;
  left: 0px;
  border-radius: 1px;
  transition: 0.5s;
}
.main-caraosel .section-caraosel .product-caraosel picture {
  width: 100%;
  height: 70%;
  display: flex;
  overflow: hidden;
  margin-bottom: 20px;
}
.main-caraosel .section-caraosel .product-caraosel picture img {
  width: 100%;
  top: -18px;
  /* width: 400px; */
  display: flex;
  right: 18px;
  border-radius: 10px;
}
.detail,
.button {
  display: flex;
  position: relative;
  top: -40px;
  margin: auto;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #0c0c0c;
  margin-left: 10px;
}
.product-caraosel .detail {
  margin-top: 45px;
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  justify-content: flex-start;
  font-size: 20px;
}
.ratting {
  width: 45px;
  margin-inline-start: 10px;
  display: flex;
  background: #e8d829;
  justify-content: center;
  flex-direction: row-reverse;
}
.product-caraosel .detail small {
  color: #141313;
}
