p.star {
  margin-right: 80px;
  width: 65%;
  font-size: 22px;
  color: #808080;
  position: relative;
  bottom: 10px;
}
@media screen and (max-width: 800px) {
  .left,
  .main-caraosel,
  .right {
    width: 100%;
  }
  .main-caraosel .text h1 {
    font-size: 35px;
  }
  .main-caraosel .text p {
    width: 90%;
  }

  .main-caraosel header h1 {
    font-size: 25px;
  }
  .main-caraosel header p span {
    font-size: 30px;
  }
  .section-caraosel .product-caraosel {
    min-width: 49%;
    margin: 0 10px 0 0;
  }
  .detail,
  .button {
    font-size: 16px;
  }
}
.content {
  display: flex;
  flex-direction: column;
}

.time-caraosel {
  left: 5px;
  position: relative;
  font-size: 16px;
  top: -40px;
  margin-left: 10px;
}
