.main-caraosel2 {
  width: 100%;
  /* height: 704px; */
  /*background: red;*/
  /* margin: -47px auto; */
  position: relative;
  padding: 108px 0;
}
.main-caraosel2 .text {
  padding-left: 25px;
  color: #554;
}
.main-caraosel2 .text h1 {
  font-size: 50px;
}
.main-caraosel2 .text p {
  width: 60%;
  padding: 5px;
  margin: auto;
  line-height: 30px;
}
.main-caraosel2 header {
  width: 98%;
  height: -10px;
  display: flex;
  align-items: center;
  padding: 20px;
  flex-direction: row;
  justify-content: space-between;
}

.main-caraosel2 header p span {
  font-size: 30px;
  margin: 0 5px;
  cursor: pointer;
  color: #161515;
  width: 40px;
  height: 40px;
  display: inline-block;
  line-height: 37px;
  text-align: center;
  border-radius: 20px;
}
.main-caraosel2 header p span:hover {
  background: #eb5757;
  color: white;
}
.section-caraosel2 {
  width: 98%;
  height: 600px;
  margin: auto;
  display: flex;
  overflow-x: auto;
}
.section-caraosel2::-webkit-scrollbar {
  display: none;
}
.section-caraosel2 .product-caraosel2 {
  min-width: 300px;
  height: 75%;
  background: whitesmoke;
  margin: 0 20px 0 0;
  position: relative;
  /* left: 0px; */
  transition: 0.5s;
  border-radius: 1px;
  padding: 7px;
}
.main-caraosel2 .section-caraosel2 .product-caraosel2 picture {
  width: 100%;
  height: 70%;
  display: flex;
  overflow: hidden;
  margin-bottom: 20px;
}
.main-caraosel2 .section-caraosel2 .product-caraosel2 picture img {
  width: 100%;
  top: -18px;
  /* width: 400px; */
  display: flex;
  right: 18px;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}
.detail,
.button {
  display: flex;
  position: relative;
  top: -40px;
  margin: auto;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #0c0c0c;
  margin-left: 10px;
}
.product-caraosel2 .detail {
  margin-top: 45px;
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  justify-content: flex-start;
  font-size: 20px;
}
.ratting {
  width: 45px;
  margin-inline-start: 10px;
  display: flex;
  background: #e8d829;
  justify-content: center;
  flex-direction: row-reverse;
}
.product-caraosel2 .detail small {
  color: #141313;
}
