p.star {
  margin-right: 80px;
  width: 65%;
  font-size: 22px;
  color: #808080;
  position: relative;
  bottom: 10px;
}
@media screen and (max-width: 800px) {
  .left,
  .main-caraosel2,
  .right {
    width: 100%;
  }
  .main-caraosel2 .text h1 {
    font-size: 35px;
  }
  .main-caraosel2 .text p {
    width: 90%;
  }

  .main-caraosel2 header h1 {
    font-size: 25px;
  }
  .main-caraosel2 header p span {
    font-size: 30px;
  }
  .section-caraosel2 .product-caraosel2 {
    min-width: 49%;
    margin: 0 10px 0 0;
  }
  .detail,
  .button {
    font-size: 16px;
  }
}
.content {
  display: flex;
  flex-direction: column;
}

.time-caraosel2 {
  left: 5px;
  position: relative;
  font-size: 16px;
  top: -40px;
  margin-left: 10px;
}
