@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap");
.main-container-registration {
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container-signup {
  max-width: 680px;
  background-color: #fff;
  border-radius: 5px;
  height: auto;
  overflow: auto;
  background: #ffffff;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
}
.container-signup .title {
  font-size: 25px;
  font-weight: 500;
  position: relative;
  color: red;
}
.container-signup .title::before {
  content: "";
  /* position: absolute; */
  /* left: 0;
  bottom: 0; */
  /* height: 3px;
  width: 30px; */
  /* border-radius: 5px; */
  /* background: linear-gradient(135deg, #71b7e6, #9b59b6); */
}
.content form .user-details {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 20px 0 12px 0;
}
form .user-details .input-box {
  margin-bottom: 15px;

  width: -webkit-fill-available;
}
.content-item {
  margin-inline: 60px;
  margin-top: 2rem;
}
form .input-box span.details {
  display: block;
  font-weight: 500;
  margin-bottom: 5px;
}
.name {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.new-input {
  margin-inline-end: 40px;
}
.user-details .input-box input {
  height: 45px;
  width: 100%;
  outline: none;
  font-size: 16px;
  border-radius: 5px;
  padding-left: 15px;
  border: 1px solid #ccc;
  border-bottom-width: 2px;
  transition: all 0.3s ease;
}
.user-details .input-box input:focus,
.user-details .input-box input:valid {
  border-color: #9b59b6;
}
form .terms-details .terms-title {
  font-size: 12px;
  font-weight: 500;
}
form .category {
  display: flex;
  width: 80%;
  margin: 14px 0;
  justify-content: space-between;
}
form .category label {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.button-registration {
  height: 45px;
  width: -webkit-fill-available;
  margin: 35px 0;
}
.button-registration input {
  height: 100%;
  width: 100%;
  border-radius: 5px;
  border: none;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 1px;
  cursor: pointer;
  transition: all 0.3s ease;
  background: rgb(248, 135, 135);
}
.button input:hover {
  /* transform: scale(0.99); */
  background: red;
}

.log {
  text-align: center;
  font-size: 13px;
  color: grey;
}
.phone-number {
  width: 100%;
  display: flex;
  flex-direction: column;
  /* height: 45px; */
}
.p-number {
  width: 100%;
  height: 45px;
  display: flex;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  padding: 0 15px;
}
.continue-text p {
  margin-top: 20px;
  position: relative;
  /* top: 194px; */
  text-align: center;
  font-weight: 300;
  font-size: 14px;
}
.continue-text p:before {
  content: "";
  display: block;
  width: 170px;
  height: 1px;
  position: absolute;
  background: #bcb9b9;
  left: 0;
  top: 50%;
}
.continue-text p:after {
  content: "";
  display: block;
  width: 170px;
  height: 1px;
  position: absolute;
  background: #bcb9b9;
  right: 0;
  top: 50%;
}
.social-icons {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}
.google-icon {
  cursor: pointer;
}
.close-span-registration {
  position: relative;
  top: -270px;
  left: 650px;
  font-size: 30px;
  cursor: pointer;
  z-index: 5;
}
.close-registration {
  color: #aaa;
}
.close-registration:hover,
.close-registration:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
.label-class {
  font-size: 12px;
}
.input-box input[type="tel"],
select {
  background: none;
  border: none;
  outline: none;
}
.p-number select {
  cursor: pointer;
}
input::placeholder {
  color: rgb(151, 147, 147);
  opacity: 1;
  font-size: 12px;
}
#togglePassword {
  position: relative;
  margin-left: 430px;
  top: -33px;
  cursor: pointer;
}
/* Custom scrollbar for WebKit browser */
.container-signup::-webkit-scrollbar {
  width: 12px; /* width of the entire scrollbar */
}

.container-signup::-webkit-scrollbar-track {
  background: #f1f1f1; /* color of the tracking area */
}

.container-signup::-webkit-scrollbar-thumb {
  background-color: #888; /* color of the scroll thumb */
  border-radius: 6px; /* roundness of the scroll thumb */
  border: 3px solid #f1f1f1; /* creates padding around scroll thumb */
}

/* Hover effect for the scrollbar thumb */
.container-signup::-webkit-scrollbar-thumb:hover {
  background-color: #555; /* dark grey color on hover */
}
.error-message {
  color: red;
  font-size: 12px;
  margin-top: 5px;
}
.signup-text a {
  color: blue;
  text-decoration: underline;
}

@media (max-width: 584px) {
  .container-signup {
    max-width: 100%;
  }

  form .user-details .input-box {
    margin-bottom: 15px;
    width: 100%;
  }
  form .category {
    width: 100%;
  }
  .content form .user-details {
    max-height: 300px;
    overflow-y: scroll;
  }
  .content {
    margin-top: 3rem;
  }
  .user-details::-webkit-scrollbar {
    width: 5px;
  }
}
@media (max-width: 459px) {
  .container-signup .content .category {
    flex-direction: column;
  }
}
@media (max-width: 950px) {
  .close-span-registration {
    top: -300px;
    left: 400px;
  }
}
