.login-modal-wrapper {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 50%;
  translate: -50% 0%;
  z-index: 100;
  display: grid;
  place-items: center;
}
.login-modal-wrapper.show {
  overflow: auto;
}
.div-close-span {
  width: max-content;
}
.close-span {
  position: relative;
  top: 60px;
  left: 420px;
  font-size: 20px;
  cursor: pointer;
  width: 30px;
  height: 30px;
  background-color: #00000059;
  border-radius: 100px;
  border: none;
  text-align: center;
}
