.continue-text {
  position: relative;
  color: #747980;
  text-align: center;
  font-size: 14px;
  font-weight: 900;
}
.continue-text span::before {
  content: "";
  display: block;
  width: 100px;
  height: 2px;
  position: absolute;
  background: #cdced1;
  left: 0;
  top: 50%;
}
.continue-text span::after {
  content: "";
  display: block;
  width: 100px;
  height: 2px;
  position: absolute;
  background: #cdced1;
  right: 0;
  top: 50%;
}
.continue-text p:before {
  content: "";
  display: block;
  width: 100px;
  height: 1px;
  position: absolute;
  background: #000000;
  left: 0;
  top: 50%;
}
.continue-text p:after {
  content: "";
  display: block;
  width: 100px;
  height: 1px;
  position: absolute;
  background: #000000;
  right: 0;
  top: 50%;
}
