.footer {
  margin-top: 3rem;
  background-color: #222;
  color: #fff;
  padding: 20px;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.5;
  /* position: absolute; */
  /* bottom: 0; */
  width: 100%;
}

/* img {
    max-width: 100px;
    height: auto;
  } */

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.follow {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  gap: 10px;
}

.social {
  display: flex;
  gap: 20px;
}

.social a {
  text-decoration: none;
}

.divider {
  height: 2px;
  background-color: #fff;
  margin: 20px auto;
  width: 100%;
}

.footer-bottom {
  background-color: #222;
  color: #fff;
  padding: 10px 20px;
  font-size: 14px;
  font-weight: 300;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-bottom-left {
  display: flex;
  align-items: center;
}

.footer-bottom-left a {
  text-decoration: none;
  color: #fff;
  margin-right: 20px; /* Add space between the links */
}

.contact-and-company {
  display: flex;
  flex-wrap: wrap;
}

.contact,
.company {
  flex-basis: calc(40% - 10px);
  margin-right: 10px;
  margin-bottom: 20px;
  margin-top: 20px;
}

.contact h4,
.company h4 {
  margin-bottom: 10px;
}

.contact-info {
  display: flex;
  align-items: center;
  gap: 10px;
}

.mail-info {
  display: flex;
  align-items: center;
  margin-top: 30px;
  gap: 10px;
}

.contact-info p {
  margin: 0;
}

.company-content {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.company a {
  text-decoration: none;
  color: white;
  margin-right: 10px;
}

.resources {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}

.resources-content {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.resources-info {
  display: flex;
  align-items: center;
}

.resources-info p {
  margin: 0;
}

.resources-info a {
  text-decoration: none;
  color: white;
  margin-right: 10px;
}

/* Responsive Styles */

/* For smaller screens (e.g., mobile devices) */
@media screen and (max-width: 767px) {
  .footer-content,
  .follow {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }

  .follow {
    margin-top: 20px;
  }

  .social {
    margin-top: 10px;
  }
}

/* For larger screens (e.g., tablets and above) */
@media screen and (min-width: 768px) {
  .footer-content,
  .follow {
    flex-direction: row;
    justify-content: space-between;
    text-align: left;
  }

  .follow {
    margin-top: 0;
  }

  .social {
    margin-top: 0;
  }
}

@media (max-width: 991px) {
  .div {
    max-width: 100%;
  }
}
