@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content-view-order {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  width: 700px;
  height: 544px;
  position: relative;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.your-order {
  font-size: 20px;
  font-family: "Poppins";
  font-weight: 600;
}
.tabs {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
}

.tabs li {
  letter-spacing: 2px;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.tabs li.active {
  border-bottom: 4px solid #f8951d;
  color: #f8951d;
}

.modal-body {
  max-height: 400px;
  overflow-y: auto;
}
.modal-body::-webkit-scrollbar {
  width: 6px;
}
.modal-body::-webkit-scrollbar-thumb {
  background-color: #eb5757;
  border-radius: 10px;
}
.modal-body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

.order {
  border-bottom: 1px solid #ddd;
  padding: 10px 0;
  padding-right: 10px;
}
.order-id {
  font-family: "Poppins";
  font-size: 16px;
  font-weight: 600;
}

.order-header {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.order-status {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.order-time {
  display: flex;
  gap: 15px;
  color: #718ebf;
  font-family: "Poppins";
  font-size: 15px;
}
.order-items {
  font-family: "Poppins";
  font-weight: 600;
}
.order-header .order-details-name {
  display: flex;
  flex-direction: column;
  font-size: 22px;
  font-weight: 600;
  font-family: "Poppins";
}

.status-ontime {
  color: #1f982a;
  padding: 10px 7px;
  background: #dafcdd;
  border-radius: 5px;
  font-size: 11px;
}

.status-delayed {
  color: #d1a017;
  padding: 10px 7px;
  background: #fff5d9;
  border-radius: 5px;
  font-size: 11px;
}

.status-cancelled {
  color: #e34301;
  padding: 10px 7px;
  background: #fce4da;
  border-radius: 5px;
  font-size: 11px;
}

.order-status button {
  margin-left: 10px;
  padding: 5px 10px;
  cursor: pointer;
  border: none;
  border-radius: 10px;
}

.order-status button:nth-child(1) {
  color: #f64c4d;
  border: grey solid 2px;
  font-family: "Poppins";
  background: white;
}

.order-status button:nth-child(2) {
  background: #f64c4d;
  color: #fff;
  padding: 10px;
  font-family: "Poppins";
}

.close-button {
  background: none;
  border: none;
  font-size: 2rem;
  cursor: pointer;
}
.order-summary-viewOrder {
  background-color: #cacaca33;
  color: grey;
  cursor: pointer;
}
.order-details-vieworder {
  background-color: #cacaca33;
  color: grey;
}
.Expandable-items-vieworder {
  display: flex;
  justify-content: space-between;
}
