@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;1,400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: none;
  outline: transparent;
  border: none;
  text-decoration: none;
}
body {
  font-family: "Poppins", sans-serif;
}
.main {
  width: 90vw;
  height: auto;
  /* border: 1px solid aqua; */
  margin: 0 auto;
  margin-top: 3rem;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  /* border: 1px solid green; */
  margin-left: 3rem;
  margin-right: 3rem;
  margin-bottom: 0.5rem;
}
nav .btn-nav {
  display: flex;
  gap: 1.2rem;
}
nav .btn-nav .login {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 1rem;
  background: #eb5757;
  border-radius: 20rem;
  color: white;
  gap: 0.5rem;
  font-weight: 500;
  font-size: 1rem;
  font-family: "Poppins", sans-serif;
}
nav .nav-items {
  display: flex;
  gap: 4rem;
  font-size: 1.1rem;
}
nav .nav-items span {
  color: black;
  position: relative;
  transition: all 0.3s ease;
  font-size: 1.1rem;
}
nav .nav-items span::after {
  content: "";
  position: absolute;
  margin: auto;
  width: 8px;
  height: 8px;
  inset: 0;
  translate: 0 1rem;
  background: red;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.3s ease;
}
nav .nav-items span:hover::after {
  opacity: 1;
}
nav .nav-items span:hover {
  color: #eb5757;
}
.btn-cart {
  font-size: 1.2rem;
  cursor: pointer;
}
.menu-bar {
  font-size: 1.3rem;
}
.hero {
  width: 100%;
  height: 100%;
  justify-content: space-between;
  display: flex;
  margin-top: 8rem;
  /* margin-top: -8rem; */
}
.hero-left {
  width: 40%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  /* margin-top: -7rem; */
}
.hero-right {
