@media (max-width: 960px) {
  .hero {
    flex-direction: column-reverse;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .hero-right .slider {
    width: 80%;
    height: 50%;
  }
  .hero-left {
    width: 100%;
    height: 100%;
    padding: 1rem;
    margin-top: 4rem;
  }
  .hero-right {
    /* width: 100%;
    height: 50%; */
    display: none;
  }
  .hero-right .images {
    width: 100%;
    height: 100%;
    /* display: none; */
  }
  .hero-right .images #img1 {
    bottom: 14vw;
    height: 16vw;
    width: 38vw;
  }
  .hero-right .images #img2 {
    bottom: 14vw;
    height: 16vw;
    right: 20vw;
    width: 30vw;
  }
  .hero-right .images #img3 {
    top: 22vw;
    left: 4vw;
    height: 10vw;
    width: 10vw;
  }
  .hero-right .images #img4 {
    height: 17vw;
    width: 9vw;
    top: 28vw;
    right: 25vw;
  }
  .hero-right .images #img5 {
    height: 5vw;
    width: 4.9vw;
    top: 18vw;
    right: 28.2vw;
  }

  .hero-content span {
    font-size: 2.5rem;
    text-wrap: wrap;
  }
  #booking {
    font-size: 2.5rem;
  }
}
