  font-family: "Poppins", sans-serif;
  font-weight: 500;
}

.search-bar {
  display: flex;
  align-items: center;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  width: 100%;
  height: 3.2rem;
  margin-top: 1rem;
  margin-left: 0rem;
}
.search-location {
  display: flex;
  align-items: center;
  gap: 1rem;
}
/* .search-location,
.search-restaurants {
  flex: 1;
} */
.search-restaurants {
  flex: 1;
}

.search-divider {
  width: 1px;
  height: 30px;
  background-color: #ccc;
  margin: 0 10px;
}
.location-icon {
  width: max-content;
}
.location-plus {
  position: relative;
  border: red;
}
#location {
  color: #b9b1b1;
  font-size: 0.8rem;
}
#searchText {
  width: 100%;
  color: #b9b1b1;
  font-size: 0.8rem;
}

aside .nav-items {
  display: flex;
  flex-direction: column;
  gap: 0;
}
aside .btn-nav {
  display: flex;
  flex-direction: column;
  gap: 0;
  margin-left: 3rem;
  margin-right: 3rem;
}
aside .btn-nav img {
  height: 30px;
  width: 30px;
}
aside .btn-cart {
  text-align: left;
}
.btn-cart:hover {
  color: #eb5757;
}
.nav-wrapper {
  backdrop-filter: blur(10px);
  position: fixed;
  top: 0;
  right: 0;
  width: 30rem;
  height: 100%;
  background-color: white;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.nav-wrapper.active {
  /* Transformed into view */
  transform: translateX(0);
}
@media (max-width: 700px) {
  .nav-wrapper {
    width: 25rem;
  }
}
