.complete-profile-container {
  padding: 85px;
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0 2px 4px 3px rgba(0, 0, 0, 0.1);
  margin: 0 auto;
  margin-top: 2rem;
  padding: 52px 76px;
}

.complete-profile-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.complete-profile-header h2 {
  color: #f44336;
  margin: 0;
}

.skip-link {
  color: #f44336;
  text-decoration: none;
}

.sub-header {
  font-size: 1.1em;
  font-weight: 700;
  color: #666;
  margin-bottom: 20px;
  display: flex;
  margin-top: 2px;
}

.profile-form .form-group {
  margin-bottom: 20px;
}

.profile-form label {
  display: flex;
  font-weight: 600;
  /* font-weight: bold; */
  margin-bottom: 5px;
}

.profile-form label span {
  color: red;
}

.input-wrapper {
  display: flex;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 5px 10px;
}

.input-wrapper box-icon {
  margin-right: 10px;
  color: #888;
}

.input-wrapper input {
  border: none;
  outline: none;
  flex: 1;
}

.input-wrapper .phone-input-complete-profile {
  display: flex;
  align-items: center;
}

.input-wrapper .phone-input-complete-profile select {
  border: none;
  outline: none;
  background: none;
  margin-right: 10px;
  font-size: 1em;
}

.input-wrapper .phone-input-complete-profile input {
  flex: 1;
  border: none;
  outline: none;
}

.terms {
  font-size: 0.9em;
  color: #666;
  display: flex;
}

.terms a {
  color: #007bff;
  text-decoration: none;
  margin: 0 5px;
}

.terms span {
  color: red;
}

.submit-button {
  width: 100%;
  height: 4rem;
  padding: 10px 0;
  background-color: #eb5757;
  color: #fff;
  border: none;
  border-radius: 10px;
  font-size: 1.2em;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #e04040;
}
